* {
  box-sizing: border-box;

}

body {
font-family: "Roboto", sans-serif;
background-color:#e7e7e9;

}


h2{
color:#2c2621;
}
.row::after {
  content: "";
  clear: both;
  display: block;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}




html {
  font-family: "Lucida Sans", sans-serif;
}

.img{
box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);

}

.header {
  background-color: #9933cc;
  color: #ffffff;
  padding: 15px;
}

.button {
  background-color: #c9ccc3;
  color: #2c2621;
  padding: 15px 32px;
  width: 100%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  border: 1px solid white;
  border-radius:7px;
}

.zoom {

  transition: transform .2s;

}

.zoom:hover {
  transform: scale(1.25); 
}

.button:hover {
  background-color: #554c3b;
  color: #e7e7e9;
  box-shadow:none;
}

.aside {
  padding: 15px;
  color: #2c2621;
  text-align: center;
  font-size: 14px;
 box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
 height:1000 px;
  border: 5px solid black;
}

.footer {
   position:absolute;
   bottom:0;
   width:100%;
   height:60px;   /* Height of the footer */
   background:#9f9a8e;
   text-align:center;
}

/* For desktop: */
.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 100%;
  }
}




